@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  
  /* font-family: -apple-system, BlinkMacSystemFont, 'Roboto Mono', "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif; */
  
  font-family: 'Roboto Mono', monospace;
  font-size: 12pt;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: url(https://images.unsplash.com/photo-1484480974693-6ca0a78fb36b?ixlib=rb-1.2.1&auto=format&fit=crop&w=1952&q=80)
  no-repeat center center fixed; 

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
    display: flex;
    justify-content: center;
    margin: 30px;
    position: relative;
}

.textform {
    display: flex;
    position: absolute;
    padding: 10px;
}

.controls {
    display: block;
    margin-top: 100px;
}

#textbox {
    width: 500px;
    font-family: 'Roboto Mono';
    font-size: 1em;
}

.toggleLabel {
    display: flex;
    align-content: center;
    padding-top: 20px;
    font-size: 0.8em;
    cursor: pointer;
}
.toDoItem {
    display: flex;
    justify-content: space-between;
    padding-top: 15px;


}
.checkboxAndText {
    display: flex;
    justify-content: left;
    margin-right: 0;

}

#deleteItemButton {
    font-size: 0.8em;
    padding: 3px;
    -webkit-transition: none;
    transition: none;
}

.toToLabel {
    display: flex;
    align-content: center;
    cursor: pointer;
}
.box {
    display: flex;
    flex-direction: column;
    height: 500px;
    width: 630px;
    border: 1px solid antiquewhite ;
    border-radius: 25px;
    padding: 20px;
    margin: 20px;
    background-color: rgba(250, 235, 215, 0.5);
    overflow: scroll;

    
}
.center {
  display: flex;
  justify-content: center;
}

.button {
  background-color: #60a3bc;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-family: "Roboto Mono";
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.button:hover {
  background-color: #60a3bc;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-family: "Roboto Mono";
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;    
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
